    slug: "maple-brown-butter-pumpkin-pie",
    note: "There are a million pumpkin pie recipes on the web. There’s even a perfectly good recipe on the back of every single can of pumpkin purée on the store shelves. So I didn’t want to make just another basic pumpkin pie. No one needs that. Instead, I made a couple simple swaps to fancy it up just enough to make you go “Oooh…” with that first bite, but not enough that it made what is normally a very easy pie into something complicated. Enter my Maple Brown Butter Pumpkin Pie.",
